.app-info{
    padding: 25px;
    background-color: #3d5a80;
    border-radius: 4px;
    box-shadow: 15px 15px 30px rgba(0,0,0, .15);
    text-align: center;
    color: white;
}

@media (max-width: 576px) {
    .app-info{
        padding: 10px;
        padding-top: 15px;
        padding-bottom: 15px;
    }
}