@import '../../../scss/media.scss';
@import '../../../scss/variables.scss';
.counter{
    position: relative;
    padding: 100px;
    padding-bottom: 200px;
    padding-top: 0;
    box-shadow: 1px 15px 15px $main-color;
    text-align: center;
    @include mobile {
        padding-left: 0;
        padding-right: 0;
    }
    &__title{
        font-size: 35px;
        margin: 60px;
        @include mobile {
            margin: 0px;
            margin-bottom: 60px;
            margin-top: 60px;
        }
    }   
    &__container{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 40px;
        @include tablet{
            flex-direction: column;
            gap: 20px;
        }
    }
    &__btn{
        width: 243px;
        height: 50px;
        cursor: pointer;
        font-size: 25px;
        color: $main-color;
        transition: 0.3s all;
        border-radius: 4px;
        border: 1px solid $main-color;
        background-color: rgb(26, 25, 25);
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px;
        svg {
            width: 32px;
            height: 32px;
            animation-name: animal;
            animation-duration: 1s;
        }
        &:hover{
            box-shadow: 1px 1px 15px 15px $main-color;
        }
        @include mobile {
            box-shadow: 1px 1px 15px 15px $main-color;
        }
    }
    &__num{
        position: absolute;
        bottom: 50px;
        left: 50%;
        font-size: 50px;
        font-weight: bold;
        transform: translate(-50%, -50%);
    }
}