@import '../../../scss/media.scss';
@import '../../../scss/variables.scss';
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    padding: 70px;
    box-shadow: 1px 15px 15px  $main-color;
    overflow-x: hidden;
    @include mobile {
        padding-left: 30px;
        padding-right: 30px;
        padding-bottom: 70px;
        padding-top: 100px;
    }
    &__svg{
        svg {
            fill: $main-color;
            width: 70px;
            height: 70px;
            object-fit: cover;
            animation: animation-svg 3s infinite linear;
        }
    }
    &__title{
        margin: 0;
        font-size: 35px;
    }
    &__container{
        display: flex;
        gap: 40px;
        margin-top: 30px;
        @include mobile {
            flex-direction: column;
        }
    }
    &__link{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
        width: 231px;
        height: 50px;
        border-radius: 4px;
        border: 1px solid $main-color;
        font-size: 25px;
        color: $main-color;
        transition: 0.3s all;
        background-color: rgb(26, 25, 25);
        text-decoration: none;
        &:hover{
            box-shadow: 1px 1px 15px 15px $main-color;
        }
    }
}
#counter-link{
    animation: animation-count 1s linear;
    @include mobile {
        animation: animation-count 0.5s linear;
    }
}
#calculator-link{
    animation: animation-calc 1s linear;
    @include mobile {
        animation: animation-calc 0.5s linear;
    }
}
@keyframes animation-svg {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
@keyframes animation-count {
    0%{
        transform: translateX(-300%);
    }
    100%{
        transform: translateX(0%);
    }
}
@keyframes animation-calc {
    0%{
        transform: translateX(300%);
    }
    100%{
        transform: translateX(0%);
    }
}