@import url('https://fonts.googleapis.com/css2?family=Xanh+Mono&display=swap');
@import '../../scss/media.scss';
.homepage{
    text-align: center;
    height: 100vh;
    margin-bottom: 60px;
    &__technologies{
        list-style-type: none;
        display: flex;
        gap: 20px;
        position: absolute;
        left: 50%;
        top: 150px;
        transform: translateX(-50%);
        @include tablet{
            left: 20%;
        }
        @include mobile{
            left: -38%;
        }
        img{
            width: 80px;
            height: 80px;
            object-fit: cover;
        }
    }
    &__item{
        padding: 10px;
        border-radius: 4px;
        transition: 0.3s all;
        &:hover{
            background-color: #fff;
            @include small-tablet{
                &:hover{
                    background-color: #3F72AF;
                }
            }
        }
    }
    &__main{
        position: absolute;
        top: 70%;
        left: 50%;
        width: 100%;
        transform: translateX(-50%);
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;
        &-span{
            font-family: 'Arial', sans-serif;
            position: relative;
            &::before{
                content: '';
                position: absolute;
                width: 36px;
                height: 36px;
                border-radius: 100%;
                background-color: rgb(0, 0, 0);
                top: -21%;
                left: -8px;
                z-index: -1;
            }
        }
        &-title{
            font-family: 'Dosis', sans-serif;
            font-size: 48px;
            @include mobile{
                font-size: 30px;
            }
        }
    }
    &__btns{
        display: flex;
        justify-content: center;
        gap: 10px;
        @include small-tablet{
            flex-direction: column;
        }
        &-link{
            height: 45px;
            width: 130px;
            background: #fff;
            box-shadow: 0 0 10px rgba(22, 22, 212, .425);
            padding: 12px 30px;
            transition: 1s all;
            position: relative;
            color: black;
            text-decoration: none;
            font-family: 'Dosis', sans-serif;
            font-weight: 700;
            @include mobile{
                &:nth-child(1){
                    display: none;
                }
            }
            }
    }
}