@import '../../scss/media.scss';

.randomCounter {
  margin-bottom: 30px;
  h2{
    text-align: center;
    margin: 40px 0px;
  }
  &__container{
    width: 350px;
    height: 300px;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 5px 5px 10px rgba(0,0,0, .2);
    background-color: #fff;
    text-align: center;
    font-size: 34px;
    margin: 0 auto;
    color: red;
    @include mobile{
      width: 310px;
  }
  }
  &__block{
    background-color: red;
    width: 100px;
    height: 100px;
    color: white;
    margin: 0 auto;
    line-height: 100px;
    border-radius: 4px;
  }
  &__controls {
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
    button{
      padding: 7px 12px;
      cursor: pointer;
      background-color: #6B7A8F;
      color: white;
      border-radius: 4px;
      border: none;
      transition: 0.3s all;
      font-size: 20px ;
      &:hover{
        background-color: rgb(255, 0, 0);
      }
    }
  }
}
  