@import '../../../scss/media.scss';
@import '../../../scss/variables.scss';
.calculator{
    padding-bottom: 100px;
    text-align: center;;
    box-shadow: 1px  15px 15px $main-color;
    &__title{
        margin: 60px;
        font-size: 35px;
        @include mobile {
            margin: 30px;
            margin-bottom: 60px;
            margin-top: 60px;
            font-size: 30px;
        }
    }
    &__container{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 30px;
        position: relative;
    }
    &__inputs{
        width: 50%;
        display: flex;
        position: relative;
        @include small-tablet{
            width: 391px;
        }
        @include mobile {
            width: 315px;
        }
    }
    &__input{
        width: 100%;
        transition: 0.3s all;
        outline: none;
        padding: 10px;
        font-size: 23px;
        color: $main-color;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        background-color: rgb(26, 25, 25);
        border: 1px solid $main-color;
        font-weight: bold;
        &::placeholder{
            color: $main-color;
        }
        &:focus, &:hover{
            box-shadow: $box-shadow;
        }
    }
    &__span{
        height: 54px;
        width: 60px;
        position: absolute;
        right: 1px;
        top: 1px;
        border-left: 1px solid $main-color;
        svg {
            transition: 0.3s all;
            fill: $main-color;
            background-color: rgb(26, 25, 25);
            height: 100%;
            width: 100%;
            border-radius: 4px;
            &:hover{
                box-shadow: 1px 1px 15px 15px $main-color;
            }
        }
    }
    &__result{
        font-size: 35px;
        text-overflow: ellipsis;
    }
    &__buttons{
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    &__btn{
        width: 243px;
        height: 50px;
        cursor: pointer;
        font-size: 25px;
        color: $main-color;
        transition: 0.3s all;
        border-radius: 4px;
        border: 1px solid $main-color;
        background-color: rgb(26, 25, 25);
        &:hover{
            box-shadow: 1px 1px 15px 15px $main-color;
        }
        @include mobile {
            width: 315px;
        }
    }
    &__reboot{
        width: 60px;
        height: 48px;
        position: absolute;
        right: 2px;
        padding: 0;
        border-left: 1px solid $main-color;
        z-index: 10;
        svg {
            transition: 0.3s all;
            border-radius: 4px;
            cursor: pointer;
            height: 100%;
            width: 100%;
            background-color: #1a1919;
            fill: $main-color;
            &:hover{
                box-shadow: 1px 1px 15px 15px $main-color;
            }
        }
    }
}