@import '../../../scss/media.scss';

.list-group-item{
    border-bottom: 1px solid #3d5a80;
    padding: 0;
    &:last-child{
        border-bottom: none;
    }
    span{
        line-height: 35px;
        font-size: 20px;
        cursor: pointer;
        min-width: 550px;
        transition: 0.3s all;
        padding: 25px 25px;
    }
    input{
        line-height: 35px;
        font-size: 20px;
        text-align: center;
        border: 0;
        transition: 0.3s all;
    }
    button{
        width: 35px;
        height: 35px;
        margin: 3px;
        font-size: 17px;
        border: none;
        cursor: pointer;
    }
    .btn-cookie{
        color: #e09f3e;
    }
    .btn-trash{
        color: #e5383b;
    }
    .fa-star{
        width: 35px;
        height: 35px;
        text-align: center;
        line-height: 35px;
        font-size: 16px;
        color: #ffd700;
        transition: 0.3s all;
        transform: translateX(30px);
        opacity: 0;
    }
    &.like .fa-star{
        opacity: 1;
        transform: translateX(0);
    }
    &.increase .list-group-item-label, &.increase .list-group-item-input{
        color: #e09f3e;
    }
    &.increase.btn-star{
        color: #aeaeae;
    }
}
/* media */
@media (max-width: 992px) {
    .list-group-item{
        span{
            min-width: 50%;
        }
        &-input{
            width: 50%;
        }
    }
}
@media (max-width: 576px) {
    .list-group-item {
        span{
            min-width: 40%;
            padding-left: 10px;
            padding-right: 10px;
            padding-top: 20px;
            padding-bottom: 20px;
        }
        &-input{
            width: 55%;

        }
    }
}
