@import '../../scss/media.scss';

.currency{
    h1, p{
        text-align: center;
        margin-bottom: 30px;
    }
    &__block{
        display: flex;
        flex-direction: column;
        width: 350px;
        background-color: #fff;
        padding: 10px;
        border-radius: 5px;
        box-shadow: 5px 5px 10px rgba(0,0,0, .2);
        margin-bottom: 40px;
        margin: 0 auto;
        @include mobile{
            width: 310px;
        }
    }
    &__output{
        margin-top: 10px;
        margin-bottom: 10px;
        text-align: center;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        background-color: red;
    }
    &__select{
        background-color: #6B7A8F;
        color: #fff;
        transition: 0.3s all;
        height: 40px;
        margin-top: 20px;
        border-radius: 4px;
        option{
            background-color: #6B7A8F;
            border-radius: 4px;
        }
    }
    &__descr{
        padding: 5px;
        margin-top: 10px;
        background-color: #6B7A8F;
    }
    input{
        margin-top: 20px;
        background-color: #6B7A8F;
        border: 2px solid transparent;
        border-radius: 4px;
        color: white;
        padding: 5px;
        height: 40px;
        &::placeholder{
        color: white;
        }
    }
    button{
        margin-top: 30px;
        display: block;
        cursor: pointer;
        border: none;
        background-color: #6B7A8F;
        padding: 5px;  
        border-radius: 4px;
        color: white;
        transition: 0.3s all;
        &:hover{
        background-color: rgb(255, 0, 0);
        }
    }
}