.app-add-form{
    margin-top: 30px;
    padding: 25px;
    background-color: #3d5a80;
    border-radius: 4px;
    box-shadow: 15px 15px 30px rgb(0 0 0 / 15%);
    color: #fff;
}
.add-form{
    margin-top: 20px;
    input{
        width: 350px;
        margin-right: 20px;
    }
}
@media (max-width: 992px) {
    .app-add-form{
        flex-wrap: wrap;
    }
    .app-form input {
        width: 100%;
    }
}
@media (max-width: 660px) {
    .add-form{
        flex-direction: column;
        gap: 20px;
    }
    .add-form input{
        width: 100%;
        margin-right: 0;
    }
}
@media (max-width: 576px) {
    .app-add-form-title{
        font-size: 19px;
    }
}